@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500&display=swap);


.graph-container{
    position: relative;
}
.graph-wrapper{
    display: flex;
    position: relative;
    max-width: 400px;
    margin: 3rem auto 0 auto;
}
.data-list{
    list-style: none;
    color: #000;
    height: 460px;
    margin: 0 15px; 
    padding: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 100%;
    position: relative;
} 
.graph-y-labels {
    font-size: clamp(12px, 2vw, 14px);
}  
.horizontal-bar{
    height: 1.2px;
    width: 100%;
    border-radius: 10px;
    background: #c4c4c4;
}
.horizontal-bar:nth-of-type(3){
    display: none;
}
.vertical-bar{
    width: 1.2px;
    height: 100%;
    border-radius: 10px;
    background: #c4c4c4;
    position: absolute;
    left: 65px;
}





@media only screen and (min-width: 850px) {
    .graph-wrapper{
        max-width: 650px;
    }
}
.table-item{
    margin: 0;
}
:root{
  --header: #474747;
  --text: #1F1B1B;
}
body{
  padding: 0  1rem 2rem 1rem;
}
#MainContent{
  text-align: center;
  font-family: 'Poppins', sans-serif;
}
h1{
  color: var(--header);
  font-weight: 400;
}
p, li{
  color: var(--text);
}
.list{
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: auto;
}
.underline{
  height: 2px;
  border-radius: 5px;
  max-width: 840px;
  background-color: #c4c4c4;
  margin: 1rem auto;
}

.disclaimer{
  text-align: right;
  font-size: 11px;
  color: #928D8D;
}
.main-title{
  font-size: 65px;
}
.main-about{
  max-width: 960px;
  margin: auto;
  text-align: justify;
}

.installation-list{
  max-width: 840px;
  text-align: left;
}
.install-img{
  height: 20px;
  margin: 0 0 0 5px;
}


.instructions-container{
  display: flex;
  justify-content: space-around;
  max-width: 1080px;
  margin: auto;
}
.instructions-list{
  max-width: 500px;
  text-align: left;
  margin: 0;
}
.demo-img{
  width: 500px;
  height: 312px;
  margin: 0;
}

@media only screen and (max-width: 1050px) {
  .instructions-container{
    flex-direction: column;
    align-items: center;
  }
  .instructions-list{
    margin-bottom: 10px;
  }
}
